import React from "react";
import { HashRouter as Router, Switch, Route, Link } from "react-router-dom";
import pic1 from "./サイレントマジョリティー.png"
import pic2 from "./プラネタリウム.png"


const Ear = () =>{
  return (
  <body>
    <section class="section">
      <div class="container">
        <h1 class="title">耳コピ（ピアノ）</h1>
        <h2 class="subtitle">
          みなさん、耳コピ（耳コピー）をしたことはありますか？耳コピとは耳できいた音楽を
          楽器などを使って再現することです。特にピアノでの耳コピは右手でメロディー、左手で
          伴奏を弾くことができるので再現率が高くより楽しむことができます。
        </h2>
        <br /><br />
        <h1 class="title">らふみみこぴとは</h1>
        <h2 class="subtitle">
          よくYoutubeやSNSに動画をあげているのをよく見ると思います。どれも完成度が高く、
          素晴らしいものばかりです。しかし、1曲丸まる最初から最後まで再現するのは大変です。
          私もたまに耳コピをしますが、1曲すべて聞き取るのは難しく挫折してしまいます。
          そんなハードルが高そうな耳コピに気軽に挑戦できるようにと作ったのが,
          <strong>らふみみこぴ</strong>です。みなさんに耳コピを少しでも楽しんで
          もらえると嬉しいです！
        </h2>
        <br /><br />
        <h1 class="title">楽譜の提供</h1>
        <h2 class="subtitle">
          諸所間違いもあるかと思いますが、私が耳コピしたものを楽譜にして提供したい
          と思います。
          あの曲のサビが弾いてみたい！この曲のイントロが好き！といったことはありませんか？
          上でも述べたように1曲全部耳コピするのは少し億劫です。
          簡単に取り組める工夫として、楽譜は1曲分ではなくサビのみなど部分ごととなっている
          ため、<strong>楽譜が短く覚えるのが簡単</strong>というのが特徴です！
          これで少しでも弾いてみたい！という願望をかなえられると思います！
        </h2>
        <br /><br />
      </div>
    </section>
  </body>
  );
}

const Score = () =>{
  return (
  <div>
  <body>
    <section class="section">
      <div class="container">
        <h1 class="title">楽譜一覧</h1>
        <h2 class="subtitle">
      　  <li>サイレントマジョリティー（サビ）</li>
          <figure className="image is-128x128">
            <img src={pic1} alt="silentmajority"></img>
          </figure>
          <br /><br />
          <li>プラネタリウム（サビ）</li>
          <figure className="image is-128x128">
            <img src={pic2} alt="planetarium"></img>
          </figure>
          <br />
        </h2>
      </div>
    </section>
  </body>
  </div>
  );
}

const Tech = () =>{
  return (
  <body>
    <section class="section">
      <div class="container">
        <span class="tag is-link is-light">Step1</span>
        <h1 class="title">メロディー</h1>
        <h2 class="subtitle">
          どんな曲でも、まずはメロディーをつかむことから始めます！原曲を聞いてメロディーを
          弾きます。どうしてもわからないときはYoutubeであれば再生速度を変えることができるので
          遅くして聞き取ったり、イヤホンをしてより音に集中する環境を作ったりします。
          ここではハモリなどは無視してコピーしていきましょう。
        </h2>
        <span class="tag is-link is-light">Step2</span>
        <h1 class="title">簡単な伴奏</h1>
        <h2 class="subtitle">
          曲を聞くときに、ベースの音を意識して聞いてみましょう。慣れていないうちは
          なかなか難しいかもしれません。楽器構成が単純な曲（少ない楽器で構成されている曲）
          だと聞き取りやすいことが多いので最初はわかりやすい曲からチャレンジしてみることを
          お勧めします。ベースが聞き取れなくても1音1音メロディーと合わせて伴奏を
          弾いてみて探っていきましょう。ベースも最初は複雑な和音は使わず、簡単な1音を
          オクターブで交互に弾くというような伴奏をします。
        </h2>
        <span class="tag is-link is-light">Step3</span>
        <h1 class="title">アレンジ</h1>
        <h2 class="subtitle">
          最後にアレンジです。ここまでメロディーと伴奏をつけました。これに装飾を加えます。
          <li>メロディー：ところどころ音を加えて和音にしてみる、ハモリの部分を弾く</li>
          <li>伴奏：メロディー同様和音にしてみたり曲調に応じてリズムを変えてみる</li>
          こんな感じです。
        </h2>
        <br /><br />
        <span class="tag is-danger is-light">Plus α1</span>
        <h1 class="title">伴奏がカギ</h1>
        <h2 class="subtitle">
          曲調は伴奏の仕方で大きく変わります。バラードの時は静かに、明るい曲の時は
          元気よく、ダイナミックな感じにするとその曲調を再現できると思います。
          リズム感を出したいときは裏拍を取ってみたりと伴奏ひとつで曲調ががらりと
          変わります。
        </h2>
        <span class="tag is-danger is-light">Plus α2</span>
        <h1 class="title">裏技</h1>
        <h2 class="subtitle">
          元の曲から聞き取ってピアノで再現するのは難しいです。そこで少し反則かも
          しれませんが、耳コピが得意な人の動画を聞くとどう再現すればいいか
          大きなヒントになります。特に手元を移している動画を見て、どの音を
          触っているのか解読するのが一番の近道です。
        </h2>
      </div>
    </section>
  </body>  
  );
}

const Gallery = () =>{
  return (
  <body>
    <section class="section">
      <div class="container">
        <h1 class="title">動画</h1>
        <h2 class="subtitle">
          
        </h2>
        <h1 class="title"></h1>
        <h2 class="subtitle">
          
        </h2>
      </div>
    </section>
  </body>  
  );
}

const Body =() =>{
  return (
    <Router>
      <div class="tabs is-medium">
        <ul>
        <li class="is-active">
          <Link to="/">らふみみこぴ</Link>
        </li>
        <li>
          <Link to="/score">楽譜一覧</Link>
        </li> 
        <li>
          <Link to="/tech">耳コピの手順</Link>
        </li>
        <li>
          <Link to="/gallery">ギャラリー</Link>
        </li>
        </ul>  
      </div>
      <Switch>
        <Route path="/score">
          <Score />
        </Route>
        <Route path="/tech">
          <Tech />
        </Route>
        <Route path="/gallery">
          <Gallery />
        </Route>
        <Route path="/">
          <Ear />
        </Route>
      </Switch>
    </Router>
  )
}

const Head = () =>{
    return(
      <section class="hero is-link is-light is-medium">
      <div class="hero-body">
        <div class="container">
          <h1 class="title">
            らふみみこぴ
          </h1>
          <h2 class="subtitle">
            piano
          </h2>
        </div>
      </div>
    </section>
    );
}


const Tabs = () =>{
  return(
    <div class="tabs is-medium">
  <ul>
    <li class="is-active"><a>らふみみこぴ</a></li>
    <li><a>楽譜一覧</a></li>
    <li><a>ギャラリー</a></li>
    <li><a></a></li>
  </ul>
</div>
  );
}






const Bottom = () =>{
    return(
        <section className="hero is-link is-light">
  <div className="hero-body">
    <div className="container">
      &copy;minori
    </div>
  </div>
</section>
    )
}


const App = () =>{
    return (
        <div>
            <Head />
            <div className="container">
              <Body />
            </div>
            <Bottom />
        </div>
    );
};

export default App;